@import 'main';
// End Of Import

.navover{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .8);
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideNav{
    padding: 24px;
    position: fixed;
    top: 0;
    right: -100%;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 9999;
    overflow: auto;
    transition: .5s;

    &.open {
        right: 0;
    }

    .opt{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang{


            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                color: #000;
                font-size: 15px;
            }
        }

        .xtr{
            position: relative;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 10px;

            .srch{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                svg{

                    path{
                        fill: #000;
                    }
                }
            }

            form{
                opacity: 0;
                visibility: hidden;
                z-index: 99;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0, .8);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                display: none;

                @include Sm{
                    display: flex;
                }

                &.open{
                    opacity: 1;
                    visibility: visible;

                    button{
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-45%);
                        margin-left: 10px;
                        width: 30px;
                        height: 30px;

                        svg{
                            margin-top: 5px;
                        }
                    }

                    input{
                        display: block;
                    }
                }

                .icon{
                    position: absolute;
                    top: 15%;

                    svg{

                        path{
                            fill: #fff;
                        }
                    }
                }

                .cont{
                    position: relative;
                    top: -15%;
                }

                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;

                    svg{


                        path{
                            fill: #000;
                        }
                    }
                }

                input{
                    border: 0;
                    border-radius: 5px;
                    width: 600px;
                    height: 45px;
                    padding: 0 25px;
                    background-color: $color-white;
                    display: none;

                    @include Xs{
                        width: 400px;
                    }

                    @include mob375{
                        width: 290px;
                    }
                }
            }
        }

        .close1{
            font-size: 20px;
            color: #84848445;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links{
        display: block;

        .list{
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;

            & > li{
                padding: 7px 15px;
                width: 100%;

                a{
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: $color-sec;
                    line-height: 1.6;
                    transition: .5s;                

                    &:hover{
                        color: $color-black;
                    }
                }

                &:not(:last-child){
                    border-bottom: 1px solid #dedede;
                }
            }
        }
    }
}

// Mob Nav

// Nav
nav{
    padding: 30px 0;
    background-color: #FFF;
    position: sticky;
    top: 0;
    z-index: 9;
    transition: .5s;

    @include mob480{
        padding: 15px 0;
    }
    
    .content-top{
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    &.scroll{
        padding: 30px 0;
        margin-top: 0;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background-color: #FFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        transition: .5s;

        @include mob480{
            padding: 15px 0;
        }

        .logo{
            img{
                filter: brightness(0);
            }
        }

        .links{

            li{

                a{
                    color: #000;
                }
            }
        }

        .search{

            svg{

                path{
                    fill: #000;
                }
            }

            input{
                color: #000 !important;
            }
        }

        .bars{

            span{
                background-color: #000;


                &:after,&:before{
                    background-color: #000;
                }
            }
        }
    }

    .logo{
        margin-left: auto;

        img{
            filter: brightness(0);
        }
    }

    .links{
        transition: .5s;

        @include Sm{
            display: none;
        }

        &.open{
            transform: translateX(200px);
            opacity: 0;
            visibility: hidden;
        }

        ul{

            display: flex;


            li{


                a{
                    font-size: 17px;
                    font-family: "frtBold";
                    color: #000;
                    transition: .5s;

                    &:hover{
                        color: #bdbdbd;
                    }
                }

                &:not(:last-child){
                    margin-left: 30px;
                }
            }
        }
    }

    .search{
        margin-right: 45px;

        svg{

            path{
                fill: #000;
            }
        }

        @include Sm{
            display: none;
        }

        .over{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 99;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        form{
            position: relative;

            & > button{
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
                transition: .5s;


                &.hide{
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }

        .serach-content{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 200px;
            transition: .5s;

            &.open{
                right: calc(-200px - -31px);
                z-index: 99;
            }

            input{
                width: 200px;
                height: 30px;
                background-color: transparent;
                border: 0;
                border-bottom: 1px solid #ccc;
                color: #000;

                &::placeholder{
                    color: rgb(212, 212, 212);
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        margin-bottom: 7px;
        cursor: pointer;
        display: none;

        @include Sm{
            display: flex;
        }

        span{
            width: 100%;
            height: 2px;
            background-color: #000;
            position: relative;
            display: block;

            &:after,&:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: #000;
            }

            &:before {
                top: 8px;
            }

            &:after {
                top: -8px;
            }
        }
    }
}
// Nav

// Start Bread

.bread{
    height: 100px;
    background-image: url("../images/bread.jpg");
    background-size: cover;
    background-position: center;

    img{
        position: absolute;
        top: 0;
        right: 0;
        z-index: -9;
        height: 100%;
        width: 100%;
    }

    .container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }

    p{
        margin: 0;
        font-family: "frtLight";

        a{
            position: relative;
            color: $color-white;
            font-family: inherit;


            &:not(:last-child) {
                margin-left: 26px;
                color: #686868;


                &:after{
                    content: "/";
                    transform: translateX(-13px);
                    display: inline-block;
                }
            }
        }
    }
}

// End Of Bread


// Purchases

// careers

.careers{
    padding: 60px 0 150px;

    @include Sm{
        padding: 50px 0;
    }

    .col-lg-6{
        &:last-child{

            @include Sm{
                order: -1
            }
        }
    }

    h2{
        font-size: 24px;
        color: rgb(0, 0, 0);
        line-height: 1.5;
        font-family: "frtBlk";
        margin-bottom: 50px;

        @include Sm{
            width: auto;
            margin-bottom: 10px;
            text-align: center;
        }
    }

    form{
        padding-left: 37px;

        @include Sm{
            padding-left: 0;
        }

        .field{
            margin-bottom: 20px;

            label{
                font-size: 15px;
                color: #000;
                margin-bottom: 10px;
                margin-right: 10px;
    
                &:after {
                    content: "*";
                    color: #ff4444;
                }

                &.custom{
                    margin: 0;
                    padding: 10px;
                    font-family: 'opensans';
                    height: 55px;
                    background-color: #fcfcfc;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    color: #adacad;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    border: 1px solid #e6e8e8;

                    &:after {
                        content: "";
                        color: transparent;
                    }

                    span{
                        display: inline-block;
                        width: 113px;
                        height: 35px;
                        line-height: 35px;
                        font-size: 15px;
                        font-family: "frt";
                        color: #8a8a8a;
                        background-color: #dedede;
                        text-align: center;
                        cursor: pointer;
                    }
                }
            }
    
            input{
                height: 55px;
                border: 1px solid #e6e8e8;
                background-color: #fcfcfc;
                border-radius: 0;
                transition: .5s;

                &:focus{
                    outline: none;
                    box-shadow: none;
                    border: 1px solid #000;
                }

                &#cv{
                    display: none;
                }
            }

            &.txt{


                p{
                    font-size: 15px;
                    color: #000;
                    margin-bottom: 10px;
                    margin-right: 10px;
                }

                textarea{
                    padding: 10px 20px;
                    width: 100%;
                    height: 135px;
                    resize: none;
                    border: 1px solid #e6e8e8;
                    transition: .5s;

                    &:focus{
                        outline: none;
                        box-shadow: none;
                        border: 1px solid #000;
                    }
                }
            }
        }

        button{
            width: 150px;
            height: 50px;
            background-color: #000;
            border: 0;
            color: #FFF;
            float: left;
            border: 1px solid transparent;
            transition: .5s;

            &:hover{
                background-color: #FFF;
                color: #000;
                border: 1px solid #e6e8e8;
            }
        }
    }

    .image{
        padding-top: 35px;

        @include Sm{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 40px;
        }
    }
}

// careers

// Purchases

// Footer
footer{
    background-color: #000;

    & > .container{
        padding: 50px 0;
        border-bottom: 1px solid #1a1a1a;

        @include mob480{
            padding: 30px 0;
        }
    }

    .content{
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
            justify-content: center;
        }
    }

    .details{
        display: flex;
        margin-right: auto;
        color: $color-white;

        @include Sm{
            margin-right: unset;
            margin-top: 35px;
        }

        @include Xs{
            flex-direction: column;
            align-items: center;
            text-align: center;
        }

        i{
            font-size: 15px;
            margin-left: 10px;
        }

        h3{
            font-size: 17px;
            font-family: "frtBlk";

            & + p{
                margin-bottom: 0;
                font-size: 15px;
                font-family: "frtLight";
            }
        }

        a{
            font-size: 15px;
            font-family: "frtLight";
            margin-bottom: 0;
            color: currentColor;
        }
    }

    .detail{

        &:not(:last-child){
            margin-left: 60px;

            @include Xs{
                margin-left: 0;
                margin-bottom: 25px;
                text-align: center;
            }
        }
    }
}

// End Of Footer

// End

.copy{
    background-color: #000;
    font-size: 13px;
    color: rgb(189, 188, 188);
    padding: 10px 0;

    .container{
        padding: 0;
    }

    .cont{
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Xs{
            padding: 0 15px;
            flex-direction: column-reverse;
            align-items: center;
        }
    }


    .content{
        display: flex;
        align-items: center;
        color: #696969;
        font-size: 13px;

        p{
            margin-bottom: 0;

            @include mob375{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }

        a{
            color: #696969;
    
            img{
                margin-right: 5px;
            }

            @include mob375{
                margin-top: 10px;
            }
        }
    }

    .links{

        @include Xs{
            margin-bottom: 15px;
        }
        

        ul{
            display: flex;

            li{
                &:not(:last-child){
                    margin-left: 20px;
                }

                a{
                    font-size: 13px;
                    font-family: 'frtLight';
                    color: #686868;
                    transition: .5s;


                    &:hover{
                        color: #FFF;
                    }
                }
                  
            }
        }
    }
}

// End